import config from '../config'
import web3instance from '../web3'

const depositEth = async (web3, amountInEther = 0) => {
  console.log('depositEth')
  return new Promise(async (resolve, reject) => {
    const instance = config.HodlDex(web3)
    const etherInWei = web3.utils.toWei(amountInEther, 'ether')
    const accounts = await web3.eth.getAccounts()
    if (accounts.length === 0) return
    instance.methods
      .depositEth()
      .send({
        from: accounts[0],
        value: etherInWei
      })
      .on('transactionHash', function (hash) {
        resolve(hash)
      })
      .on('receipt', function (receipt) {
        console.log('receipt')
      })
      .on('confirmation', function (confirmationNumber, receipt) {
        console.log('confirmation')
      })
      .on('error', function (err) {
        console.log('error')
      })
  })
}
const withdrawEth = async (web3, amountInEther = 0) => {
  console.log('withdrawEth')
  try {
    const instance = config.HodlDex(web3)
    const etherInWei = web3.utils.toWei(amountInEther.toString(), 'ether')
    const accounts = await web3.eth.getAccounts()
    if (accounts.length === 0) return
    return instance.methods.withdrawEth(etherInWei).send({
      from: accounts[0]
    })
  } catch (err) {
    console.log('[withdrawEth]', err)
  }
}

const hodlTIssue = async (web3, amount = 0) => {
  // convert HODLC to HODLT
  console.log('in HODLTIssue', amount)
  try {
    const instance = config.HodlDex(web3)
    const accounts = await web3.eth.getAccounts()
    if (accounts.length === 0) return
    return instance.methods.hodlTIssue(web3.utils.toWei(amount, 'ether')).send({
      from: accounts[0]
    })
  } catch (err) {
    console.log('[hodlTIssue]', err)
  }
}
const approveHodlT = async (web3, amount = 0) => {
  try {
    const hodlTinstance = config.HodlT(web3)
    const accounts = await web3.eth.getAccounts()
    if (accounts.length === 0) return
    const { HTOKEN_RESERVE_PROXY } = config.refreshBlockchainInfraInfo()
    return hodlTinstance.methods
      .approve(
        HTOKEN_RESERVE_PROXY,
        web3.utils.toWei(amount.toString(), 'ether')
      )
      .send({
        from: accounts[0]
      })
  } catch (err) {
    console.log('[approveHodlT]', err)
  }
}
