const claimEthDistribution = async (web3) => {
  console.log('in claimEthDistribution')
  try {
    web3 = web3instance(web3)
    const instance = config.HodlDex(web3)
    const accounts = await web3.eth.getAccounts()
    if (accounts.length === 0) return
    const DEFAULT_GAS = config.DEFAULT_GAS
    const ethAmt = await instance.methods
      .claimEthDistribution()
      .send({ from: accounts[0], gas: DEFAULT_GAS })
    console.log('claim ethAmt', ethAmt)
  } catch (err) {
    console.log('[claimEthDistribution]', err)
  }
}

