import React from 'react'
import { Link } from 'react-router-dom'
import { withTranslation } from 'react-i18next'
import networkConfig from '../../global-config'
import JoinModal from '../JoinUs/Modal'

const navItem = [
  {
    tag: 'trade-page',
    route: '/trade',
    name: 'trade'
  },
  {
    tag: 'wallet-page',
    route: '/wallet',
    name: 'wallet'
  },
  {
    tag: 'explorer-page',
    route: '/explorer',
    name: 'explorer'
  },
  {
    tag: 'calculator-page',
    route: '/calculator/simple',
    name: 'calculator'
  },
  {
    tag: 'join-us-page',
    route: '',
    name: 'joinUs'
  }
]

class Navigator extends React.Component {
  state = {
    showModal: false
  }

  hideModal = () => {
    this.setState({ showModal: false })
    // history.push('/')
  }

  renderLink = () => {
    const { t } = this.props
    return navItem.map((ele) => {
      if (!networkConfig.enableCalculator && ele.name === 'calculator') {
        return ''
      } else if (ele.name === 'joinUs') {
        return (
          <div
            key={ele.route}
            className="item"
            onClick={() => this.setState({ showModal: true })}
          >
            <img
              alt={ele.tag}
              src={require(`../../assets/images/${ele.tag}.svg`)}
            />
            <p className="nav-title">{t(`navigator.${ele.name}`)}</p>
          </div>
        )
      } else {
        return (
          <Link key={ele.route} to={ele.route} className="item">
            <img
              alt={ele.tag}
              src={require(`../../assets/images/${ele.tag}.svg`)}
            />
            <p className="nav-title">{t(`navigator.${ele.name}`)}</p>
          </Link>
        )
      }
    })
  }

  getCurrentLanguage = () => {
    return localStorage.getItem('i18nextLng')
  }

  render() {
    return (
      <div className="side-nav">
        <div className="item-container">
          <div className="top-group">
            <div className="nav-logo">
              <img
                alt="logo"
                src={require('../../assets/images/HODLC_LogoFiles-04.svg')}
              ></img>
              <p className="logo-title">HODL DEX</p>
            </div>
            {this.renderLink()}
          </div>
          <div className="bottom-group">
            {/* <div className="item" onClick={()=> this.setState({showModal: true})}>
              <img
                alt="setting"
                src={require(`../../assets/images/setting.svg`)}
              ></img>
              <p className="nav-title">{t(`navigator.settings`)}</p>
            </div> */}
            <div className="info-container">
              <div className="info-item">
                <span className="info-dot"> &#9679;</span>
                <a
                  className="info-link"
                  href="https://hodlcommunity.org/wp-content/uploads/2020/12/HODLCommunity-Terms-of-Use.pdf"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Terms of Use
                </a>
              </div>
              <div className="info-item">
                <span className="info-dot"> &#9679;</span>
                <a
                  className="info-link"
                  href="https://drive.google.com/file/d/141XYEy_KD_DoqeV0p8fLH3PQ9LPw4w1C/view"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Whitepaper
                </a>
              </div>
              <div className="info-item">
                <span className="info-dot"> &#9679;</span>
                <a
                  className="info-link"
                  href="https://metamask.io/download.html"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  MetaMask
                </a>
              </div>
              <div className="info-item">
                <span className="info-dot"> &#9679;</span>
                <a
                  className="info-link"
                  href="https://trustwallet.com/deeplink/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Trust Wallet
                </a>
              </div>
            </div>
            {this.state.showModal && (
              <JoinModal
                activeStatus={this.state.showModal}
                hideModal={this.hideModal}
                t={this.props.t}
              />
            )}
          </div>
        </div>
      </div>
    )
  }
}

export default withTranslation()(Navigator)
