import React from 'react'
import ReactDOM from 'react-dom'

class Modal extends React.Component {
  render() {
    const clickDimmer = this.props.clickDimmer || this.props.hideModal
    const activeState = this.props.activeState ? 'active' : ''
    const extraClassName = this.props.className || '' // if you want white dimmer, add 'visible' extra className
    return ReactDOM.createPortal(
      <div
        className={`ui dimmer modals ${activeState} ${extraClassName}`}
        onClick={clickDimmer}
      >
        <div
          onClick={(e) => e.stopPropagation()}
          className={`ui small modal visible ${activeState} ${extraClassName} main-modal`}
        >
          <div className="ui top attached big label modal-title">
            {this.props.title || 'Modal'}
          </div>
          <div className="modal-body">
            {this.props.body}
            <div className="modal-button-group">
              {this.props.buttonLeft || (
                <button
                  onClick={this.props.hideModal}
                  className="ui button bg-modal-button cancel"
                >
                  'Cancel'
                </button>
              )}
              {this.props.buttonRight || (
                <button
                  onClick={this.props.hideModal}
                  className="ui button bg-modal-button highlight"
                  type="submit"
                >
                  "OK"
                </button>
              )}
            </div>
          </div>
        </div>
      </div>,
      document.querySelector('#modal')
    )
  }
}

export default Modal
