export const getUserBalances = (web3, account) => async (dispatch) => {
  if (!web3) return
  try {
    const response = await connector.getUserBalances(web3, account)
    dispatch({
      type: GET_USER_BALANCES,
      payload: response
    })
  } catch (err) {
    console.error(err)
  }
}

