import React, { useState, useEffect } from 'react'
import Modal from '../../components/Modal'
import { Switch, Route, Link } from 'react-router-dom'
import { withTranslation } from 'react-i18next'
import config from '../../global-config'
import Simple from './simple'
import Recurring from './recurring'
import web3instance from '../../connector/web3'
import connector from '../../connector'
const AVERAGE_LENGTH = 7

const Calculator = ({ web3, t, match, rates }) => {
  const [showDisclaimerModal, setShowDisclaimerModal] = useState(true)
  const [avgTransaction, setAvgTransaction] = useState('...(calculating)')

  useEffect(() => {
    document.title = 'HODL DEX | Calculator'
  }, [])

  useEffect(() => {
    async function getEvents() {
      if (avgTransaction > 0) return
      const localweb3 = web3instance(web3)
      const height = await localweb3.eth.getBlockNumber()
      const from = height - (AVERAGE_LENGTH * 24 * 60 * 60) / 15 // past 24 hrs
      const filter = [from, 'latest', 'global']
      const evnts = await connector.getEvents(localweb3, ...filter)
      const eventCount = evnts.filter((evt) => {
        return evt.type === 'buy HODLC'
      }).length
      const avg = Math.floor(eventCount / AVERAGE_LENGTH)
      setAvgTransaction(avg)
    }
    setTimeout(() => {
      getEvents()
    }, 500)
  }, [avgTransaction, rates])

  return (
    <div className="calulator-component container-fluid">
      {showDisclaimerModal && (
        <Modal
          className="main-modal-wide"
          activeState={showDisclaimerModal}
          hideModal={() => setShowDisclaimerModal(false)}
          title={t('calculator.disclaimer.title')}
          clickDimmer={() => {
            console.log(
              'Cannot close the disclaimer under you explicitly take actions.'
            )
          }}
          body={
            <div className="disclaimer-paragraph">
              {config.metadata.calculatorDisclaimer}
            </div>
          }
          buttonLeft={
            <a href="/">
              <button className="ui button bg-modal-button cancel">
                Leave
              </button>
            </a>
          }
          buttonRight={
            <button
              onClick={() => setShowDisclaimerModal(false)}
              className="ui button bg-modal-button highlight"
              type="submit"
            >
              I Agree
            </button>
          }
        ></Modal>
      )}
      <div className="session-body">
        <div className="ui header session-header">Calculator</div>
        <div className="calculator-type-switcher">
          <Link
            to={`${match.url}/simple`}
            className={`item hodling ${!window.location.href.includes(
              'recurring'
            )}`}
          >
            <div>Simple Mode</div>
          </Link>
          <Link
            className={`item recurring ${window.location.href.includes(
              'recurring'
            )}`}
            to={`${match.url}/recurring`}
          >
            <div>Recurring Mode</div>
          </Link>
        </div>
        <div>
          <Switch>
            <Route
              path={`${match.path}/simple`}
              component={() => (
                <Simple
                  avgDay={AVERAGE_LENGTH}
                  avgTransaction={avgTransaction}
                  rates={rates}
                />
              )}
            />
            <Route
              path={`${match.path}/recurring`}
              component={() => (
                <Recurring
                  avgDay={AVERAGE_LENGTH}
                  avgTransaction={avgTransaction}
                  rates={rates}
                />
              )}
            />
          </Switch>
        </div>
      </div>
    </div>
  )
}

export default withTranslation()(Calculator)
