import React from 'react'
import { connect } from 'react-redux'

import config from '../../config'
import OrderBook from '../../connector/orderBook'
import List from './list'

const INIT_STATE = {
  book: [],
  type: ''
}

class Book extends React.Component {
  state = INIT_STATE

  componentWillUnmount() {
    clearInterval(this.myinterval)
  }

  componentDidMount() {
    this.initBook()
    try {
      this.myinterval = setInterval(() => {
        this.updateBook()
      }, config.rqstInteval.order)
    } catch (e) {
      console.log(e)
    }
  }

  updateBook = () => {
    const book = new OrderBook(this.props.web3)
    book.removeAllListeners()
    let temp = []
    book.on('init', () => {
      temp = []
    })
    book.on('firstOrder', (payload) => {
      temp = [payload]
    })
    book.on('newOrder', (payload) => {
      temp.push(payload)
    })
    book.on('done', (payload) => {
      this.setState({ book: temp })
    })
    book.subscribe(this.props.type, this.props.user)
  }

  initBook = () => {
    const book = new OrderBook(this.props.web3)
    book.removeAllListeners()
    book.on('init', () => {
      this.setState(INIT_STATE)
    })
    book.on('firstOrder', (payload) => {
      this.setState({ book: [payload] })
    })
    book.on('newOrder', (payload) => {
      this.setState({ book: [...this.state.book, payload] })
    })
    book.subscribe(this.props.type, this.props.user)
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.type !== this.props.type) return this.updateBook()
  }

  render() {
    const { address } = this.props
    const orders = address
      ? this.state.book.filter((b) => b[0] === address)
      : this.state.book
    const hasItems = orders.length > 0 || this.props.address
    if (hasItems) {
      return (
        <div className="book">
          <div className="list-container">
            <List
              type={this.props.type}
              orders={orders}
              total={orders.length || 0}
            />
          </div>
        </div>
      )
    } else {
      return <></>
    }
  }
}

const mapStateToProps = ({ blockchain }) => {
  return {
    web3: blockchain.web3
  }
}

export default connect(mapStateToProps)(Book)
